  padding-top: 1rem;
  font-weight: bold;
  margin: 0 0 0.5rem 0;
  color: #f2b705;
  color: var(--yellow);
  flex: 2 1;
  display: inline-block;
}

button {
  padding: 0.3rem;
  border-radius: 5px;
  border: 0;
  box-shadow: 2px 2px 2px black;
  cursor: pointer;
  color: white;
  font-weight: bold;
}

.App_startBtn__z5XBZ {
  height: 3rem;
  width: 3rem;
  display: inline-block;
  -webkit-transform: scale(1.2) translateX(2rem);
          transform: scale(1.2) translateX(2rem);
  background-color: #d90416;
  background-color: var(--red);
  border-radius: 50%;
  margin: auto;
  z-index: 3;
  outline: none;
}

button:active {
  -webkit-transform: scale(1.2) translate(2rem, 4px);
          transform: scale(1.2) translate(2rem, 4px);
}

.App_error__3wEi4 {
  font-weight: bolder;
  color: #bf1b39;
  color: var(--redder);
  flex: 0.5 1;
}

/* Game phase */
.App_game__24zci {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(200px, auto);
}

.App_game__24zci button {
  border-radius: 0;
  display: block;
  box-shadow: none;
  border: 1px solid black;
  font-size: 2rem;
}

.App_game__24zci button:active {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
}

.App_game__24zci button:first-of-type {
  background-color: green;
}
.App_game__24zci button:last-of-type {
  background-color: red;
}

.App_error__3wEi4 {
  font-size: 1.5rem;
  position: absolute;
  left: 0;
  top: 0;
  background-color: white;
}

